.portfolio {
  .container {
    padding-top: $sec-pad-mob;
    padding-bottom: $sec-pad-mob;

    @media screen and (min-width: $tablet) {
      padding-top: $sec-pad-tab;
      padding-bottom: $sec-pad-tab;
    }

    @media screen and (min-width: $desktop) {
      padding-top: $sec-pad-desk;
      padding-bottom: $sec-pad-desk;
    }
  }

  &-title {
    @extend %visually-hidden;
  }
}

.portfolio-text {
  position: relative;
  padding-left: 80px;
  margin-bottom: 44px;
  @include font(600, 11px, 1.91, 0.1em);
  text-transform: uppercase;

  @media screen and (min-width: $tablet) {
    line-height: 1.36;
  }
}

.sub-title.portfolio-text {
  color: $primary-grey-color;
}

.sub-title.portfolio-text::before {
  background-color: $primary-grey-color;
}

.portfolio-list {
  @include flex();
  flex-wrap: wrap;

  @media screen and (min-width: $tablet) {
    margin: -15px;
  }
}

.portfolio-list-item {
  @media screen and (max-width: $tablet - 1) {
    width: 418px;
  }

  @media screen and (min-width: $tablet) {
    width: calc((100% - 60px) / 2);
    margin: 15px;
  }

  @media screen and (min-width: $desktop) {
    width: calc((100% - 120px) / 4);
  }

  &:not(:last-child) {
    @media screen and (max-width: $tablet - 1) {
      margin-bottom: 30px;
    }
  }
}
